// general font
body {
  font-family: $font;
  color: $textcolor;
  overflow-x: hidden;
}

// wrapper (c'est explicite ici mais on commente quand même :d)
.wrapper {
  width: $mainwrapper;
  margin: 0 auto;

  @include laptop {
    width: 98%;
    margin: 0 auto;
  }
}

.hwrap {
  @include laptop {
    width: 550px;
  }

  @include mobile {
    width: 98%;
  }
}

// inner wrapper
.iwrapper {
  width: 90%;
  margin: 0 auto;
}

.iwrapper50 {
  width: 50%;
  margin: 0 auto;

  @include laptop {
    width: 90%;
  }
}

// titles size
.verybig {
  font-size: 56px;
}

.big {
  font-size: 34px;
}

.medium {
  font-size: 24px;
}

.small {
  font-size: 18px;
}

// special text color
.grey {
  color: $greytext;
}

.white {
  color: white;
}

.yellow {
  color: $yellow;
}

.lessyellow {
  color: $lessyellow;
}

// fontsize
.f-reg {
  font-size: 16px;
}

.f-medium {
  font-size: 14px;
}

.f-small {
  font-size: 12px;
}

// font thin
.fwnormal {
  font-weight: normal;
}

.fwbold {
  font-weight: bold;
}

// text transform uppercase
.ttu {
  text-transform: uppercase;
}

// background image sizes (maybe, not sure yet) 
// .bg-big
// .bg-medium
// .bg-small

// general links & other links
a, .gen-links {
  text-decoration: none;
  color: $textcolor;
  transition: $transit;

  &:hover {
    color: $yellow;
  }
}

.link {
  text-decoration: none;
  transition: $transit;

  &:hover {
    color: $yellow;
  }
}

// background colors
.greybg {
  background-color: $somebg;
}

.whitebg {
  background-color: white;
}

.specialbg {
  background: url('../img/price-bg.png') center/cover;
}

// center text
.center {
  text-align: center;
}

.tend {
  text-align: right;
}

// pointer
.pointer {
  cursor: pointer;
}

// paddings
.lpad-l {
  padding-left: $topbot-little;
}

.rpad-l {
  padding-right: $topbot-little;
}

.lpad-m {
  padding-left: $leftright-medium;
}

.rpad-m {
  padding-right: $leftright-medium;
}

// margins
.topbot-l {
  margin-top: $topbot-big;
  margin-bottom: $topbot-big;
}

.topbot-m {
  margin-top: $topbot-medium;
  margin-bottom: $topbot-medium;
}

.topbot-s {
  margin-top: $topbot-little;
  margin-bottom: $topbot-little;
}

// center (icenter -> y for Y axis)
.ycenter {
  position: relative;
  top: 50%;
  transform: translateY(-50%);
}

// top and bottom big margin
.auto-tb {
  margin-top: $autotb;
  margin-bottom: $autotb;
}

.bigbot {
  margin-bottom: $bigbot;
}

.minibot {
  margin-bottom: $minitb;
}

// hidden element
.hidden {
  opacity: 0;
  transition: $transit;
}

// Background animations
// just scale (zoom)
.bg-zoom {
  transform: scale(100%);
  transition: $transit;

  &:hover {
    transform: scale(110%);
  }
}

// scale & rotate
.bg-rot {
  transform: scale(100%) rotate(0deg);
  transition: $transit;

  &:hover {
    transform: scale(110%) rotate(5deg);
  }
}

// Custom price section
.customprices {
  position: relative;
  
  h2 {
    margin: 2px 0;
  }
  
  p {
    margin-bottom: 50px;
  }
}

.rightbg {
  background: url('../img/about-img.jpg') center/cover;
  height: 50vh;
}

// forms
input {
  border: rgb(165, 165, 165) solid 1px;
  height: 50px;
  margin-bottom: 12px;

  &::placeholder {
    padding-left: 5px;
    font-size: 14px;
  }
}

textarea {
  border: rgb(165, 165, 165) solid 1px;

  &::placeholder {
    padding-left: 5px;
    padding-top: 5px;
    font-size: 14px;
  }
}